body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

/* Universal Styles */

html {
	font-size: 18px;
}

input:focus {
	outline: none;
}

/* Reusable Component - Image Container */

.image-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	overflow: hidden;
	background: #000;
}

.image-container img {
	width: 100%;
}

/* App Styles */

.App {
	font-family: 'Work Sans', sans-serif;
}

/* Home Button */
h1 {
	padding: .66rem 0;
	text-align: center;
	background-color: #cca353;
	font-family: Poppins, sans-serif;
	font-size: 1.8rem;
	color: #fff;
}

/* .headerDiv {
	margin: -18px auto;
} */

/* Search Bar  */
.SearchBar {
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-image: url('../visual_aids/background_search_desktop.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	height: 25rem;
}

.SearchBar-sort-options ul {
	display: flex;
	justify-content: center;
	margin-bottom: 2.22rem;
	color: #ffffff;
}

.SearchBar-sort-options li {
	list-style-type: none;
	cursor: pointer;
	width: 4.33rem;
	border-bottom: 1px solid #fff;
	padding: 0 2.58rem .33rem 2.58rem;
	line-height: 1.13;
	text-align: center;
	font-weight: 600;
	font-size: .83rem;
	transition: color .25s;
}

.SearchBar-sort-options li:hover {
	color: #d4cfcf;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
	border-bottom: 1px solid #f0c36c;
	color: #f0c36c;
}

.SearchBar-fields {
	display: flex;
	justify-content: center;
	margin-bottom: 2.88rem;
}

.SearchBar-fields input {
	width: 21rem;
	padding: .66rem 1rem;
	margin-right: 2.22rem;
	border: 1px solid #fff;
	border-radius: 4px;
	font-size: .77rem;
	font-weight: 500;
}

.SearchBar-fields input:last-child {
	margin-right: 0;
}

.SearchBar-submit {
	text-align: center;
}

.SearchBar-submit button {
	border-radius: 4px;
	padding: .72rem 1.7rem;
	background-color: #cca353;
	color: #ffffff;
	font-weight: 600;
	transition: background-color .5s;
}

.SearchBar-submit a:hover {
	cursor: pointer;
	background-color: #a7874b;
}

/* Business List  */

.BusinessList {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	margin: 4.4rem 10%;
}

/* Business  */

.Business {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 16.66rem;
	margin: 0 .5rem 2.3rem .5rem;
	border: .1rem solid #000;
    padding: 1rem;
}

.Business .image-container {
	height: 16.66rem;
	margin-bottom: 1rem;
}

.Business h2 {
	margin-bottom: .5rem;
	font-size: 1.2rem;
	font-weight: 600;
}

.Business-information {
	display: flex;
	justify-content: space-between;
}

.Business-information p {
	font-size: .88rem;
	font-weight: 300;
	line-height: 1rem;
}

.Business-address {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.Business-reviews {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	text-align: right;
}

.Business-reviews h3 {
	color: #cca353;
	font-weight: 600;
}

.Business-reviews .rating {
	font-size: .88rem;
	line-height: 1rem;
}

/* Review  */

.reviewText {
	width: 25rem;
	text-align: center;
}

/* Media query  */

@media only screen and (max-width: 560px) {
	html {
		font-size: 15px;
	}

	.SearchBar {
		background-image: url('../visual_aids/background_search_mobile.jpg');
	}

	.SearchBar-sort-options ul {
		margin-left: 2rem;
		margin-right: 2rem;
	}

	.SearchBar-fields {
		flex-direction: column;
		align-items: center;
	}

	.SearchBar-fields input {
		margin-right: 0;
		margin-bottom: .86rem;
	}
}
